import React from 'react'

import Layout from '../../components/layouts/Layout'
import StandartPageLayout from '../../components/layouts/StandartPageLayout'
import SEO from '../../components/Seo'

import MainWidget from '../../components/common/Widgets/MainWidget'
import TransactionOrders from '../../components/common/TransactionOrders'

const BriefcasePage = ({ location }) => (
    <Layout location={location}>
        <SEO title="Торговля" keywords={['gatsby', 'application', 'react']} />
        <StandartPageLayout location={location}>
            <MainWidget location={location} mobile />
            <br />
            <TransactionOrders />
        </StandartPageLayout>
    </Layout>
)

export default BriefcasePage
